import React, {useEffect, useState, useRef} from 'react'
import {connect} from 'react-redux'
import {withApiService} from '../hoc'
import cn from 'classnames';
import './style.css'

const Me = (props) => {
    const {apiService} = props;
    const {name, id, token, avatarUrl} = props.user;
    const avatarFileInput = useRef(null);

    const [avatarFormActive, setAvatarFormStatus] = useState(false);
    const [avatarPreviewImage, setAvatarPreviewImage] = useState(null);

    const onFileUpload = (e) => {
        const file = e.target.files[0];
        if (file) {
            setAvatarFormStatus(true);

            const reader = new FileReader();
            reader.readAsDataURL(file);

            reader.onloadend = function () {
                const previewImage = reader.result;
                setAvatarPreviewImage(previewImage);
            };

        } else {
            setAvatarFormStatus(false);
        }
    };

    const resetAvatarForm = () => {
        setAvatarFormStatus(false);
        avatarFileInput.current.value = null;
    };

    const uploadAvatar = (e) => {
        e.preventDefault();

        const data = new FormData();
        data.append('avatar', avatarFileInput.current.files[0]);
        data.append('id', id);

        apiService.uploadAvatar(data, token).then(e => {
            console.log('avatar uploaded');
            resetAvatarForm();
        })
    };

    const avatarClasses = cn({
        'profile__avatar-form': true,
        'active': avatarFormActive
    });

    return (
        <div className="profile">
            <div className="profile__top">
                <div className="profile__background" style={{backgroundImage: 'url(https://images2.alphacoders.com/209/thumb-1920-209080.jpg)'}}>

                </div>
                <div className="profile__info">
                    <div className="container">
                        <div className="profile-info">
                            <div className="profile-info__list">
                                <div className="profile-info__item">
                                    <div className="profile-info__value">
                                        0
                                    </div>
                                    <div className="profile-info__key">
                                        Followers
                                    </div>
                                </div>
                                <div className="profile-info__item">
                                    <div className="profile-info__value">
                                        0
                                    </div>
                                    <div className="profile-info__key">
                                        Following
                                    </div>
                                </div>
                                <div className="profile-info__item">
                                    <div className="profile-info__value">
                                        0
                                    </div>
                                    <div className="profile-info__key">
                                        Photos
                                    </div>
                                </div>
                                <div className="profile-info__item">
                                    <div className="profile-info__value">
                                        0
                                    </div>
                                    <div className="profile-info__key">
                                        Posts
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="profile__avatar">
                    <div className="container">
                        <div className="profile__avatar-image" style={{backgroundImage: `url(/images/${avatarUrl})`}}>
                            <div className={avatarClasses}>
                                <form onSubmit={uploadAvatar} className="avatar-form">
                                    <label className="avatar-form__label">
                                        Upload a new photo
                                        <input ref={avatarFileInput} onChange={onFileUpload} type="file"/>
                                    </label>
                                    <div className="avatar-form__content">
                                        <div className="avatar-form__preview" style={{backgroundImage: `url(${avatarPreviewImage})`}}>

                                        </div>
                                        <div className="avatar-form__actions">
                                            <div className="avatar-form__apply">
                                                <button type="submit" className="btn">Apply</button>
                                            </div>
                                            <div className="avatar-form__cancel">
                                                <div onClick={resetAvatarForm} className="btn btn-danger">Cancel</div>
                                            </div>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="profile__bottom">
                <div className="container">
                    <div className="profile__name">{name}</div>
                    {/*<div className="profile__actions">*/}
                    {/*    <div className="profile__follow">*/}
                    {/*        <div className="btn">Follow</div>*/}
                    {/*    </div>*/}
                    {/*</div>*/}
                    <div className="profile__wall">
                        <div className="profile__wall-bar">

                        </div>
                        <div className="profile__wall-left">
                            <div className="profile-wall">
                                <div className="profile-wall__create">
                                    <div className="post-create">
                                        <div className="post-create__icon">
                                            <div className="post-icon">
                                                <i className="fa fa-comments-o" aria-hidden="true" />
                                            </div>
                                        </div>
                                        <div className="post-create__wrap">
                                            <div className="post-create__input">
                                            <textarea placeholder="Write a new post..." className="textarea-theme" name="" id="" cols="30" rows="10">

                                            </textarea>
                                            </div>
                                            <div className="post-create__actions">
                                                <div className="post-create__action">
                                                    <button className="btn">
                                                        <i className="fa fa-picture-o"
                                                           aria-hidden="true" />
                                                    </button>
                                                </div>
                                                <div className="post-create__action">
                                                    <button className="btn">Post</button>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="profile-wall__list">
                                    <div className="profile-wall__item">
                                        <div className="profile-post">
                                            <div className="profile-post__icon">
                                                <div className="post-icon post-icon_green">
                                                    <i className="fa fa-picture-o" aria-hidden="true" />
                                                </div>
                                            </div>
                                            <div className="profile-post__head">
                                                <div className="profile-post__head-left">
                                                    <span className="profile-post__name">Denis</span> posted a photo
                                                </div>
                                                <div className="profile-post__head-right">
                                                    22 min ago
                                                </div>
                                            </div>
                                            <div className="profile-post__image">
                                                <img src="https://vodabereg.ru/wp-content/uploads/2016/08/serfing.jpg" alt=""/>
                                                <div className="post-info">
                                                    <div className="post-info__comments">
                                                        <i className="fa fa-comment-o" aria-hidden="true"/>21 Comments
                                                    </div>
                                                    <div className="post-info__likes">
                                                        <i className="fa fa-heart-o" aria-hidden="true"/> 12 Likes
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="profile-wall__item">
                                        <div className="profile-post">
                                            <div className="profile-post__icon">
                                                <div className="post-icon post-icon_tomato">
                                                    <i className="fa fa-pencil" aria-hidden="true"/>
                                                </div>
                                            </div>
                                            <div className="profile-post__head">
                                                <div className="profile-post__head-left">
                                                    <span className="profile-post__name">Denis</span> posted an article
                                                </div>
                                                <div className="profile-post__head-right">
                                                    55 min ago
                                                </div>
                                            </div>
                                            <div className="profile-post__body">
                                                <div className="profile-post__content">
                                                    Lorem ipsum dolor sit amet, consectetur adipisicing elit. Aliquam amet at consequatur, corporis delectus deserunt, distinctio error expedita facilis magnam, odio odit placeat rerum soluta vel veritatis voluptas. Blanditiis, molestias!
                                                </div>
                                            </div>
                                            <div className="profile-post__info">
                                                <div className="post-info">
                                                    <div className="post-info__comments">
                                                        <i className="fa fa-comment-o" aria-hidden="true"/> 21 Comments
                                                    </div>
                                                    <div className="post-info__likes">
                                                        <i className="fa fa-heart-o" aria-hidden="true"/> 12 Likes
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {/*<h2>Me</h2>*/}
            {/*<div className="me pt-4">*/}
            {/*    <div className="me__block">*/}
            {/*        <div className="me__avatar">*/}
            {/*            <img src="https://img2.freepng.ru/20180626/fhs/kisspng-avatar-user-computer-icons-software-developer-5b327cc98b5780.5684824215300354015708.jpg" alt=""/>*/}
            {/*            <div className="me__avatar-edit">*/}
            {/*                <div className="avatar-loader">*/}
            {/*                    <div className="avatar-loader__left">*/}
            {/*                        <input className="form-control" placeholder="Image URL" type="text"/>*/}
            {/*                    </div>*/}
            {/*                    <div className="avatar-loader__right">*/}
            {/*                        <button className="btn btn-primary">Edit</button>*/}
            {/*                    </div>*/}
            {/*                </div>*/}
            {/*            </div>*/}
            {/*        </div>*/}
            {/*        <div className="me__info">*/}
            {/*            <div className="me__name">*/}
            {/*                Name: Den*/}
            {/*            </div>*/}
            {/*        </div>*/}
            {/*    </div>*/}
            {/*</div>*/}
        </div>
    )
};

const mapStateToProps = (state) => {
    return {
        user: state.user
    }
};

export default withApiService(connect(mapStateToProps)(Me))
